*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  border-radius: 0;
}

a {
  font-family: "Roboto";
  text-transform: uppercase;
  text-decoration: none;
}

h1 {
  font-family: "Inter";
  font-size: 3rem;
  line-height: 4.5rem;
  font-weight: 700;
  color: #4F4F4F;
  margin: 1rem 0rem;
}

h2 {
  font-family: "Inter";
  color: #333333;
  font-size: 2.1875rem;
  line-height: 1.2em;
  font-weight: 400;
}

h3 {
  font-family: "Inter";
  color: #333333;
  font-size: 1.5rem;
  line-height: 1.2em;
  font-weight: 400;
}

p {
  font-family: "Inter";
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 1.5em;
  color: #5A6B7B;
  margin: 1rem 0rem;
}

select {
  font-family: "Roboto";
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.2em;
}

.container {
  max-width: 1195px;
  margin: 0 auto;
  display: flex;
  padding: 2rem 0rem;
}
@media (max-width: 599px) {
  .container {
    padding: 1rem 2rem;
  }
}

.button {
  display: inline-block;
  border-radius: 5px;
}

.button-primary {
  color: #fff;
  font-size: 0.9375rem;
  line-height: 1.375rem;
  padding: 1rem 2rem;
  background-color: #0288D1;
  border-bottom: 4px solid #0277BD;
  transition: all 0.3s ease-in;
}
.button-primary:hover {
  background-color: #09a7fd;
}

.navbar {
  position: fixed;
  width: 100%;
}
.navbar .container {
  justify-content: space-between;
  height: 100%;
}
.navbar .nav-logo {
  margin-right: 3rem;
}
.navbar .nav-menu {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.navbar .nav-menu__left a:last-child, .navbar .nav-menu__right a:last-child {
  margin-right: 0;
}
.navbar .nav-menu a {
  text-decoration: none;
  color: #5A6B7B;
  text-transform: uppercase;
  margin: 0 1.5rem;
  font-size: 0.8125rem;
}
.navbar .nav-menu a:hover {
  color: #718597;
}
.navbar .nav-menu .btn {
  border: 1px solid #03A9F4;
  padding: 0.8125rem 2.5rem;
  color: #03A9F4;
  border-radius: 3px;
}
.navbar .nav-menu .btn:hover {
  color: #2ebcfc;
  border-color: #2ebcfc;
}

select {
  padding: 1rem 1.875rem;
  width: 17.8125rem;
  border-radius: 5px;
  background: #EFF4F7;
  background: url("../../../img/caret-down.svg") no-repeat right #EFF4F7;
  -webkit-appearance: none;
  background-position-x: 90%;
  border: 1px solid #C6D2D9;
}

.hero {
  min-height: 43.75rem;
  background: linear-gradient(#EAF8FF, #fff);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
}
.hero__left--out {
  width: calc((100vw - 1195px) / 2);
}
.hero__left--in {
  flex: 1.2;
}
.hero__right--in {
  flex: 2;
  display: flex;
  justify-content: flex-end;
}
.hero .hero__review {
  display: flex;
  align-items: center;
}
.hero .hero__review span {
  font-family: "Inter";
  font-size: 1.125rem;
  color: #546E7A;
  margin-left: 1rem;
}

.trusted__text {
  font-size: 1.125rem;
  line-height: 1.625rem;
  color: #7F9CAD;
  display: flex;
  justify-content: center;
  width: 100%;
}
.trusted__text:before, .trusted__text:after {
  content: "";
  flex: 1;
  height: 0;
  border-top: solid rgba(127, 156, 173, 0.3) 1px;
}
.trusted__text:before {
  margin: auto 1em auto auto;
}
.trusted__text:after {
  margin: auto auto auto 1em;
}

.icons .container {
  justify-content: space-between;
}
.icons__item {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.icons__item span {
  font-family: Roboto;
  font-size: 2.75rem;
  line-height: 1.4em;
  color: #3D4853;
}

.features .container {
  flex-direction: column;
}
.features__heading {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.25rem;
  align-items: center;
}
.features__list {
  display: flex;
  flex-direction: column;
}
.features__list .features__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #C6D2D9;
}
.features__list .item_info h3 {
  margin-top: 1.25rem;
}
.features__list .item_info p {
  font-family: Roboto;
  text-transform: uppercase;
  font-size: 0.875rem;
  line-height: 1.2em;
  color: #3D4853;
  margin-top: 0.625rem;
  margin-bottom: 1.25rem;
}